body {
  // background-color: aqua;
  font-family: Roboto;
  font-weight: 400;
}
.page-header {
  width: 100%;
}

.header-container {
  max-width: 1024px;

  padding-top: 41px;
  padding-bottom: 97px;

  height: auto;
  margin-left: auto;
  margin-right: auto;

  background-position: center;
  background-size: cover;
  background-image: url(../images/Rectangle1.jpg);
  background-repeat: no-repeat;
}
.container {
  width: 100%;

  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 320px) {
    width: 320px;
    padding-left: 20px;
    padding-right: 20px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding-left: 75px;
    padding-right: 75px;
  }

  @media screen and (min-width: 1024px) {
    width: 1024px;
    padding-left: 71px;
    padding-right: 71px;
  }
}
.logo-container {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}
.logo {
  display: block;
  font-weight: 500;
  font-size: 30px;
  line-height: 1.17;

  color: #ffffff;
  margin-left: 10px;
}

.logo-box {
  display: flex;
  align-items: center;
}

.search-input {
  margin-bottom: 0;
  width: 336px;
  height: 20px;
  margin-top: 37px;
  display: flex;
  margin-left: auto;
  margin-right: auto;

  background: transparent;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;

  &::placeholder {
    font-size: 14px;
    line-height: 1.14;
    color: #ffffff;
  }
}

.link {
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
  letter-spacing: 0.02em;
  color: #ffffff;
  text-transform: uppercase;
}

// base
a {
  text-decoration: none;
}

ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

nav {
  display: flex;
  align-items: flex-end;
}

nav .link {
  position: relative;
}

nav .link:not(:last-child) {
  margin-right: 39px;
}

nav .link::after {
  position: absolute;
  bottom: -2px;
  border-radius: 3px;
  content: '';
  display: block;
  width: 100%;
  height: 4px;
  opacity: 0;
  background-color: #ff6b08;
  // transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

nav .link.current::after {
  opacity: 1;
}

.link:hover::after,
.link:focus::after {
  opacity: 1;
}

.form-container {
  position: relative;
}
.search-icon {
  position: absolute;
  top: 4px;
  right: 272px;
}

@media screen and (min-width: 320px) and (max-width: 766px) {
  .logo {
    display: none;
  }
  .search-input {
    // width: 280px;
    width: 100%;
  }
  .search-icon {
    position: absolute;
    top: 4px;
    right: 1px;
  }
}

@media screen and (min-width: 766px) and (max-width: 1023px) {
  .search-icon {
    position: absolute;
    top: 4px;
    right: 141px;
  }
}
